menu.navbar-collapse.collapse {
    margin-top: 11px! important;
}


     .drop-menu {
         max-height: 100%;
         padding: 0px;
     }
     .drop-menu ul {
         padding: 0px;
         position: relative;
         z-index: 999;
     }
     .drop-menu ul li {
         position: relative;
         list-style: none;

     }
     .drop-menu li a {
         width: 100%;
     }

     .drop-menu li a {
    font-weight: bold;
    box-shadow: 0px 0px 1px #999;
    border-radius: 1%;
    display: table;
    list-style: none outside none;
    padding: 13px 0 15px 19px;
    text-decoration: none;
    text-transform: uppercase;
    background-color: #ffffff;
     }
     .drop-menu > ul > li:last-child a {
         border-bottom: 1px solid #E0E0E0;
     }
     .drop-menu li.active a {} .drop-menu li ul {
         left: 120%;
         opacity: 0;
         visibility: hidden;
         position: absolute;
         top: 0;
         width: 113%;
         transition: all 0.5s ease 0s;
         -webkit-transition: all 0.5s ease 0s;
     }
     .drop-menu li:hover > ul {
         opacity: 1;
         visibility: visible;
         left: 100%;
     }
     /*.drop-menu ul ul:before,*/
     
     .drop-menu ul li.menu-item-has-children:after,
     .drop-menu ul li.page_item_has_children:after {
         content: ">";
         display: block;
         overflow: visible;
         position: absolute;
         top: 17px;
         left: 90%;
         color: #5c5c5c;
         font-size: 18px;
         transform: rotate(90deg);
         -webkit-transform: rotate(90deg);
         opacity: 1;
         font-size: 12px;
         text-shadow: 1px 0px 1px rgba(0, 0, 0, 0.5);
         visibility: visible;
         transition: all 0.5s ease 0s;
         -webkit-transition: all 0.5s ease 0s;
     }
     .drop-menu ul li.menu-item-has-children:hover:after,
     .drop-menu ul li.page_item_has_children:hover:after {
         transform: rotate(0deg);
         -webkit-transform: rotate(0deg);
     }

 

